.product-container {
    width: 320px;
    padding-right: 20px;
    padding-left: 20px;
    text-align: center;
    & .main-title {
        margin-bottom: 30px;
    }
}
.product-list {
    width: 280px;
    & .ice {
        background-color: $product1-color;
    }
    & .coffe {
        background-color: $product2-color;
    }
    & .shake {
        background-color: $product3-color;
    }
}

.product-item {
    width: 100%;
    height: 485px;
margin-bottom: 16px;
    border: none;
}